import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { FormattedMessage } from 'react-intl'
import CartHeader from './components/CartHeader'
import ProgressBar from './components/ProgressBar'
import CouponErrorAdvice from './components/CouponErrorAdvice'
import ItemsList from './components/ItemsList'
import CartSummary from './components/CartSummary'
import CartCoupon from './components/CartCoupon'
import Suggestions from './components/Suggestions'
import SectionLoader from '../../common/SectionLoader'
import Button from '../../common/Button'
import { Backdrop, BlockWrapper, Content, EmptyCart, Scrollable, Wrapper } from './styled'

export class Cart extends PureComponent {
  static propTypes = {
    isPos: PropTypes.bool,
    currency: PropTypes.string,
    removeItem: PropTypes.func,
    updateItemQuantity: PropTypes.func,
    setPickup: PropTypes.func,

    isSomethingFetching: PropTypes.bool,
    isOpened: PropTypes.bool,
    lineItems: PropTypes.array,
    orderExists: PropTypes.bool,
    shippingStatus: PropTypes.object,

    setCurrentOrder: PropTypes.func,
    cartToggle: PropTypes.func,
    loadSuggestions: PropTypes.func,

    onContinue: PropTypes.func,

    estimatedTotal: PropTypes.number,
    subtotal: PropTypes.number,
    adjustments: PropTypes.array,
    estimates: PropTypes.arrayOf(
      PropTypes.shape({
        type: PropTypes.string,
        amount: PropTypes.number,
        label: PropTypes.string,
        currency: PropTypes.string,
        displayAmount: PropTypes.string,
        eligible: PropTypes.bool,
        id: PropTypes.string
      })
    ),
    // coupon
    setCouponCode: PropTypes.func,
    // couponError
    couponCodeError: PropTypes.bool,
    dismissCouponCodeError: PropTypes.func,

    // suggestions
    addSuggestionItem: PropTypes.func,
    isFetchingSuggestion: PropTypes.func,
    suggestions: PropTypes.array
  }

  static defaultProps = {
    isOpened: false
  }

  componentDidMount () {
    const { orderExists, setCurrentOrder, loadSuggestions } = this.props

    !orderExists && setCurrentOrder()
    loadSuggestions()
  }

  handleBackdropClick = () => {
    const { cartToggle } = this.props

    cartToggle(false)
  }

  render () {
    if (!this.props.orderExists) return null

    const {
      couponCodeError, isSomethingFetching, isOpened, lineItems, onContinue, cartToggle, isPos, removeItem,
      updateItemQuantity, setPickup, estimatedTotal, subtotal, estimates, adjustments, setCouponCode, currency,
      dismissCouponCodeError, addSuggestionItem, isFetchingSuggestion, suggestions
    } = this.props
    const { freeShipping, total, progress } = this.props.shippingStatus

    return (
      <div>
        {isOpened && <Backdrop onClick={this.handleBackdropClick} />}
        <Wrapper isOpened={isOpened}>
          <SectionLoader isFetching={isSomethingFetching}>
            <Content>
              <CartHeader cartToggle={cartToggle} lineItems={lineItems} >{
                !!lineItems.length &&
                <FormattedMessage
                  id={freeShipping ? 'cart.header.freeShippingReached' : 'cart.header.advice'}
                  values={{ label: total - progress }}
                />
              }
              </CartHeader>
              <ProgressBar total={total} progress={progress} />
              <Scrollable>
                {lineItems.length
                  ? <ItemsList items={lineItems} isPos={isPos} remove={removeItem}
                               updateQuantity={updateItemQuantity} setPickup={setPickup} currency={currency} />
                  : <EmptyCart><FormattedMessage id='cart.empty' /></EmptyCart>
                }
                <Suggestions addSuggestion={addSuggestionItem} isFetching={isFetchingSuggestion} items={suggestions}/>
              </Scrollable>
              {!!lineItems.length &&
              <div>
                <CartSummary currency={currency} subtotal={subtotal} estimatedTotal={estimatedTotal}
                  estimates={estimates} adjustments={adjustments}
                />
                <BlockWrapper>
                  <CouponErrorAdvice visible={couponCodeError}  dismiss={dismissCouponCodeError}/>
                  <CartCoupon setCouponCode={setCouponCode} />
                  <Button onClick={onContinue}>
                    <FormattedMessage id='cart.actions.continue'/>
                  </Button>
                </BlockWrapper>
              </div>
              }
            </Content>
          </SectionLoader>
        </Wrapper>
      </div>
    )
  }
}
export default Cart
